<template>
  <div v-cloak id="consult">
    <div class="content">
      <div class="header">
        <span>GoEasy客服</span>
        <img src="./images/pull.svg" @click="closeConsultBox"/>
      </div>
      <div class="body">
        <div v-if="qrCode.visible" class="qr-code">
          <div class="mask"></div>
          <div class="qr-code-content">
            <span>微信扫码，关注后开始沟通~</span>
            <div class="qr-code-avatar">
              <img v-show="qrCode.value" class="qr-code-icon" :src="qrCode.value"/>
            </div>
            <span>关掉浏览器，微信也能收到回复!</span>
          </div>
        </div>
        <div class="left">
          <div ref="scrollView" id="scroll" class="chat">
            <div>
              <div class="history">
                <div v-if="history.allLoaded" class="history-loaded">
                  <span>已全部加载</span>
                </div>
                <div v-else @click="loadHistory(false)">
                  <div v-if="history.loading" class="history-loaded">
                    <img src="./images/loading.gif"/>
                  </div>
                  <div v-else class="history-loaded">
                    <img src="./images/history.svg"/>
                    <span>查看更多消息</span>
                  </div>
                </div>
              </div>
              <div v-for="(message,index) in history.messages" :key="index">
                <div class="message-time"
                     v-if="showTime(index)">
                  {{formatDate(message.timestamp)}}
                </div>
                <div :class="[message.senderId === currentUser.id?'consult-record-self': 'consult-record']">
                  <img v-if="message.senderId !== currentUser.id" class="avatar" :src="message.senderData.avatar"
                       alt="头像"/>
                  <div class="info">
                    <span class="name" v-if="message.senderId !== currentUser.id">{{message.senderData.name}}</span>
                    <div class="message">
                      <div v-if="message.type === 'text'" class="text">
                        {{message.payload.text}}
                      </div>
                      <img v-if="message.type === 'image'"
                           @click="showImage(message.payload.url)" class="image"
                           :src="message.payload.url"
                           :style="{height:Math.ceil(100/(message.payload.width/message.payload.height))+'px'}"/>
                      <a class="downLoad" download title="点击下载文件"
                         v-if="message.type === 'file'" :href="message.payload.url">
                        <img class="file" src="./images/file.svg"/>
                        <div class="file-info">
                          <div class="file-info-name">{{message.payload.name |
                            fixContent}}
                          </div>
                          <div class="file-info-size">{{message.payload.size |
                            fixFileSize}}
                          </div>
                        </div>
                      </a>
                      <div class="video" v-if="message.type === 'video'"
                           @click="showVideo(message.payload.video.url)">
                        <img v-show="message.status !=='failed' && message.status !=='pending'"
                             :class="message.status ==='pending' ? 'thumbnail_default' : 'thumbnail'"
                             :style="{height:Math.ceil(100/(message.payload.video.width/message.payload.video.height))+'px'}"
                             :src="message.payload.thumbnail.url">
                        <img class="play-icon" src="./images/play.png"/>
                      </div>
                      <img v-show="message.status === 'fail'" class="status"
                           src="./images/failed.png"/>
                      <img v-show="message.status === 'sending'" class="status"
                           src="./images/pending.gif"/>
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="welcome.visible">
                <div class="consult-record">
                  <img class="avatar" src="./images/logo.png" alt="头像"/>
                  <div class="info">
                    <div class="message">
                      <div class="text" v-html="welcome.message"></div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="operation-list">
            <div class="operation-item">
              <div class="operation-item-box">
                <emoji-picker @emoji="chooseEmoji">
                  <div class="emoji-invoker" slot="emoji-invoker" slot-scope="events"
                       @click.stop="events.events.click">
                    <img src="./images/emoji.svg">
                  </div>
                  <div slot="emoji-picker" slot-scope="data">
                    <div class="emoji-picker">
                      <div>
                        <div v-for="(emojiGroup, category) in data.emojis" :key="category">
                          <div v-if="category !=='Frequently used'" class="emojis">
                                <span v-for="(emoji, emojiName) in emojiGroup" :key="emojiName"
                                      @click="data.insert(emoji)" :title="emojiName">{{ emoji }}</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </emoji-picker>
                <div class="upload-img">
                  <input accept="image/*" ref="files" @change="sendImageMessage" type="file"
                         class="image-input"/>
                </div>
                <div class="upload-video">
                  <input accept="video/mpeg,video/mp4,video/3gpp" ref="files"
                         @change="sendVideoMessage" type="file" class="image-input"/>
                </div>
                <div class="upload-file">
                  <input @change="sendFileMessage" ref="file" type="file" class="image-input"/>
                </div>
              </div>
            </div>
            <div class="input-box">
                  <textarea @paste="paste" @keydown="enterSendMessage" v-model="text" class="consult-input" type="text"
                            placeholder="请输入消息，按Enter键发送，Shift+Enter键进行换行"></textarea>
            </div>
            <div @click="sendTextMessage()" class="consult-send-btn">发送</div>
          </div>
        </div>
        <div class="right">
          <div class="icon-box">
            <img class="icon" src="./images/logo.png"/>
          </div>
          <div class="notice-title">工作时间：09:00~18:00</div>
          <div class="notice-title">公告：无</div>
          <div class="version">{{ version }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import EmojiPicker from 'vue-emoji-picker'
  import GoEasy from "../lib/goeasy.min";
  import Api from "./api/api.ts";
  import UserInfoCollector from "./userinfo-collector.ts";
  import { version } from '../../package.json'

  export default {
    name: 'consult',
    components: {
      EmojiPicker
    },
    data() {
      return {
        version: version,
        qrCode: {
          value: '',
          visible: false,
        },
        //todo:不就是一个变量放在这里而已嘛？，不需要渲染吧？
        welcome: {
          message: '',
          visible: true
        },
        currentUser: {
          id: "",
          data: {
            name: "",
            avatar: ""
          }
        },
        team: {
          id: 'goeasy-customer-support',
          data: {
            name: "GoEasy Support Team",
            avatar: ""
          }
        },
        history: {
          messages: [],
          allLoaded: false,
          loading: false
        },
        text: "",
        to: {},
        goEasy: null,
        config: null,
        api: null
      }
    },
    async created() {
      this.config = JSON.parse(decodeURIComponent(this.$route.query.cs_customer_config));
      this.api = new Api(this.config);
      this.goEasy = GoEasy.getInstance({
        host: this.config.goEasy_host,
        appkey: this.config.goEasy_appKey,
        modules: ["im", "pubsub"]
      });

      let {data} = await this.api.getWelcome();
      this.welcome.message = data;
      await this.initUserInfoAndConnectGoEasy();
      this.to = {
        type: GoEasy.IM_SCENE.CS,
        id: this.team.id,
        data: this.team.data,
      };
    },
    computed: {
      showTime() {
        return (index) => (index === 0 || (this.history.messages[index].timestamp - this.history.messages[index - 1].timestamp > 5 * 60 * 1000));
      }
    },
    filters: {
      fixContent: function (content) {
        if (content) {
          return content.length >= 20 ? content.substring(0, 10) + '...' + content.substr(-10) : content;
        }
      },
      fixFileSize: function (size) {
        if (size === 0) {
          return '1 B';
        } else {
          let k = 1000, // or 1024
            sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'],
            i = Math.floor(Math.log(size) / Math.log(k));
          return (size / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];
        }
      }
    },
    methods: {
      async initUserInfoAndConnectGoEasy() {
        let userInfoCollector = new UserInfoCollector(this.goEasy, this.initUserInfoAndConnectGoEasy, this.qrCode, this.config.accountGuid, this.api);
        let collected = await userInfoCollector.isCollected();
        if (collected) {
          this.currentUser = userInfoCollector.userInfo();
          this.connectGoEasy();
        } else {
          userInfoCollector.collect();
        }
      },
      async connectGoEasy() {
        await this.connect();
        this.initListeners();
        this.loadHistory(true);
        this.markAsRead();
      },
      async connect() {
        let otpData = await this.api.getOTP();
        this.goEasy.connect({
          id: this.currentUser.id,
          data: this.currentUser.data,
          otp: otpData.data,
          wxmpId: {
            appid: 'wxa812036f6cd4f0c5',
            openid: this.currentUser.id,
          },
          onSuccess: function () {
            console.log("GoEasy connect successfully.");
          },
          onFailed: function (error) {
            console.log("Failed to connect GoEasy, code:" + error.code + ",error:" + error.content);
          },
          onProgress: function (attempts) {
            console.log("GoEasy is connecting", attempts);
          }
        });
      },
      initListeners() {
        document.addEventListener('visibilitychange', () => {
          if (!document.hidden) {
            this.callParentFunction('clearNotificationTitle');
          }
        });
        this.goEasy.im.on(GoEasy.IM_EVENT.CS_MESSAGE_RECEIVED, this.onMessageReceived);
      },
      onMessageReceived(message) {
        if (message.type !=='CS_ACCEPT'&& message.type!=='CS_END'&& message.type!=='CS_TRANSFER') {
          this.welcome.visible = false;
          this.history.messages.push(message);
          this.scrollToBottom();
          this.createNotification(message);
          if (document.visibilityState !== 'visible') {
            this.callParentFunction('showPopup');
          }
        }
      },
      createNotification (message) {
        if (document.visibilityState === 'visible') {
          return
        }
        const notification = {
          title: 'GoEasy',
          body: '您有一条新消息',
        }
        this.callParentFunction('createNotification',notification);
      },
      enterSendMessage(event) {
        if (event.shiftKey && event.keyCode === 13) {
          event.preventDefault();
          this.text = this.text + "\n";
        } else if (event.keyCode === 13) {
          event.preventDefault();
          this.sendTextMessage();
        }
      },
      paste(e) {
        let file = e.clipboardData.files[0];
        if (file) {
          this.sendImage(file);
        }
      },
      sendMessage(message) {
        this.welcome.visible = false;
        this.history.messages.push(message);
        this.scrollToBottom();
        this.goEasy.im.sendMessage({
          message: message,
          onSuccess: () => {
            console.log("发送成功");
          },
          onFailed: (err) => {
            console.log("send message err:", err);
          }
        })
      },
      sendTextMessage() {
        if (this.text.trim().length > 0) {
          this.goEasy.im.createTextMessage({
            text: this.text,
            to: this.to,
            onSuccess: (message) => {
              this.sendMessage(message);
            },
            onFailed: (err) => {
              console.log("create text message err:", err);
            }
          });
          this.text = "";
        }
      },
      sendFileMessage(e) {
        const file = e.target.files[0];
        const fileMessage = this.goEasy.im.createFileMessage({
          file: file,
          to: this.to,
        });
        this.sendMessage(fileMessage);
      },
      sendImageMessage(e) {
        let file = e.target.files[0];
        this.goEasy.im.createImageMessage({
          file: file,
          to: this.to,
          onSuccess: (message) => {
            this.sendMessage(message);
          },
          onFailed: (err) => {
            console.log("create file message err:", err);
          }
        });
      },
      sendVideoMessage(e) {
        let file = e.target.files[0];
        this.goEasy.im.createVideoMessage({
          file: file,
          to: this.to,
          onSuccess: (message) => {
            this.sendMessage(message);
          },
          onFailed: (err) => {
            console.log("create video message err:", err);
          }
        });
      },
      chooseEmoji(emoji) {
        this.text += emoji;
      },
      showImage (url) {
        this.callParentFunction('showImage', {url:url});
      },
      showVideo (url) {
        this.callParentFunction('showVideo',{url:url});
      },
      markAsRead() {
        this.goEasy.im.markMessageAsRead({
          type: GoEasy.IM_SCENE.CS,
          id: this.team.id,
          onSuccess: function () {
            console.log('标记已读成功');
          },
          onFailed: function (error) {
            console.log("标记已读失败", error);
          }
        });
      },
      loadHistory(firstLoad) {
        this.history.loading = true;
        let lastMessageTimeStamp;
        let lastMessage = this.history.messages[0];
        if (lastMessage) {
          lastMessageTimeStamp = lastMessage.timestamp;
        }
        let limit = 10;
        this.goEasy.im.history({
          type: GoEasy.IM_SCENE.CS,
          id: this.team.id,
          lastTimestamp: lastMessageTimeStamp,
          limit: limit,
          onSuccess: (result) => {
            this.history.loading = false;
            let messages = result.content.filter((message) => {
              return message.type !=='CS_ACCEPT'&& message.type!=='CS_END'&& message.type!=='CS_TRANSFER';
            });
            if (messages.length === 0) {
              this.history.allLoaded = true;
            } else {
              if (lastMessageTimeStamp) {
                this.history.messages = messages.concat(this.history.messages);
              } else {
                this.history.messages = messages;
              }
              if (firstLoad) {
                this.markAsRead();
                this.scrollToBottom();
              }
            }
          },
          onFailed: (error) => {
            this.history.loading = false;
            console.log('load history err:', error);
          }
        });
      },
      scrollToBottom() {
        this.$nextTick(() => {
          let scroll = document.getElementById("scroll");
          scroll.scrollTop = scroll.scrollHeight;
        });
      },
      formatDate(t) {
        t = t || Date.now();
        let time = new Date(t);
        let str = time.getMonth() < 9 ? ('0' + (time.getMonth() + 1)) : (time.getMonth() + 1);
        str += '-';
        str += time.getDate() < 10 ? ('0' + time.getDate()) : time.getDate();
        str += ' ';
        str += time.getHours();
        str += ':';
        str += time.getMinutes() < 10 ? ('0' + time.getMinutes()) : time.getMinutes();
        return str;
      },
      closeConsultBox () {
        this.callParentFunction('switchPopup');
      },
      callParentFunction (functionName, parameters) {
        const targetOrigin = document.referrer;
        window.parent.postMessage({
          'function': functionName,
          'parameters': parameters,
        }, targetOrigin);
      },
    }
  }
</script>

